@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
@import "components/_pagination.scss";
@import "components/_tabs.scss";
@import "components/_grid.scss";
.m-section{
  padding: 5.5em 0;
}

._flex,
._flex-between,
._flex-center{
  display: flex;
  align-items: center;
  &._start{
    align-items: unset;
  }
}

._flex-between{
  justify-content: space-between;
}

._flex-center{
  justify-content: center;
}

._mt48{
  margin-top: 3em;
}

._mt32{
  margin-top: 2em;
}

._mt24{
  margin-top: 1.5em;
}

._scale-img{
  overflow: hidden;
  &:hover{
    img{
      transform: scale(1.05);
    }
  }
  img{
    transition: all .6s;
  }
}

.m-backdrop{
  position: relative;
  &:after{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.12);
    pointer-events: none;
    border-radius: inherit;
  }
}

.socials{
  display: flex;
  align-items: center;
  &__item{
    width: 2.5em;
    height: 2.5em;
    border-radius: 50%;
    border: 1px solid rgba(255, 255, 255, 0.10);
    background: #1A1A1A;
    @include lastM(0 .75em 0 0);
    @include center;
    &:hover{
      path{
        fill-opacity: 1;
        &:not(._not-fill){
          fill: #fff;
        }
      }
    }
  }
}


@media screen and (max-width: 992px){
  .m-section{
    padding: 1.875em 0;
  }
}


