.header{
  padding: 1.75em 0;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 20;
  color: $white;
  overflow: hidden;
  &:before{
    opacity: 0;
    transition: all .3s;
  }
  &._fix{
    background: linear-gradient(90deg,#fb9c4e 0,#eb2149 50%,#a02eb2 100%);
    &:before{
      opacity: 1;
    }
  }
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 11.5em;
    position: relative;
    z-index: 2;
  }
  &__logo{
    display: block;
    flex-shrink: 0;
    width: 15.625em;
    img{
      width: 100%;
      display: block;
    }
  }
  .try-free__shops{
    display: none;
  }
}

.burger{
  display: none;
}

.nav{
  display: flex;
  align-items: center;
  &__link{
    font-size: 1.25em;
    padding: 0 .8em;
    display: flex;
    align-items: center;
    height: 1.5em;
    border-radius: 1.1em;
    background-color: $white30;
    margin-left: .7em;
    &:hover{
      background-color: $white70;
      color: $dark300;
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    padding: .625em 0;
    overflow: unset;
    &:before{
      animation: unset;
    }
    &__inner{
      //justify-content: s;
      padding: 0;
    }
    &__logo{
      width: 11.25em;
    }
    .try-free__shops{
      display: flex;
      padding-top: 1.375em;
    }
    .try-free__shop{
      &:nth-child(1){
        order: 2;
        margin: 0 0 0 .9375em;
      }
    }
    &._open{
      .menu{
        opacity: 1;
        transform: translateX(0);
      }
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
    }
  }

  .burger{
    width: 2.75em;
    height: 2.75em;
    @include center;
    margin-right: 1em;
    position: relative;
    z-index: 12;
    img{
      width: 100%;
      transition: all .3s;
    }
    ._view2{
      @include centerAbsolute;
      opacity: 0;
    }
  }

  .menu{
    transform: translateX(-100%);
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $white;
    color: $black;
    padding: 4.25em 1em 2em;
    transition: all .4s;
    &__scroll{
      overflow-y: auto;
      height: 100%;
    }
  }

  .nav{
    display: block;
    &__link{
      height: 2.8em;
      border-radius: 0;
      background-color: transparent;
      color: $black;
      justify-content: center;
    }
  }
}


