.try-free{
  margin: 0 auto;
  width: 62.1875em;
  color: $white;
  transition: all .3s ease-out;
  &__inner{
    border-radius: 5.5em;
    padding: .1875em;
    background: linear-gradient(90deg, #FB9C4E 0%, #EB2149 50%, #A02EB2 100%);
    box-shadow: 0 0 2.75em rgba(183, 42, 145, 0.75) inset, 0 0 2.75em rgba(183, 42, 145, 0.75);
    transition: all .3s ease-out;
    width: 100%;
    &:hover{
      width: calc(100% + 5.5em);
      .try-free__inner2{
        padding: 0 2.5em;
      }
    }
  }
  &__inner2{
    border-radius: inherit;
    background: #261E39;
    transition: all .3s ease-out;
  }
  &__wrap{
    padding: 3.75em 5.5em;
    transition: width .3s ease-out;
    display: grid;
    grid-template-columns: 15.3125em calc(100% - 15.3125em - 2.75em);
    gap: 2.75em;
  }
  &__col2{
    display: flex;
    flex-direction: column;
  }
  &__qr{
    padding-bottom: 100%;
    position: relative;
    border-radius: 1.75em;
    border: 3px solid $light300;
    background: $dark300;
    img{
      @include centerAbsolute;
      width: calc(100% - 3.8em);
    }
  }
  &__title{
    font-size: 3.75em;
    line-height: 121%;
    font-weight: 300;
    font-family: $font2;
    span{
      background: linear-gradient(90deg, #FB9C4E 0%, #EB2149 50%, #A02EB2 100%);
      background-clip: text;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      font-weight: 800;
    }
  }
  &__subtitle{
    color: $blue100;
  }
  &__shops{
    display: flex;
    padding-top: 1.25em;
    margin-top: auto;
  }
  &__shop{
    height: 5em;
    padding: .875em 1.25em;
    border-radius: 0.875em;
    border: 2px solid $light300;
    background: $dark300;
    margin-right: 1.25em;
    &:last-child{
      margin: 0;
    }
    svg{
      width: auto;
      height: 100%;
      display: block;
    }
    &:hover{
      background: $white;
      border-color: $white;
      path:not(._not-hover){
        fill: $black;
      }
    }
  }
}

._anchor{
  &__target{
    position: absolute;
    bottom: 100%;
    margin-bottom: 5em;
    pointer-events: none;
  }
}

.m-icon{
  width: 16.35em;
  height: 16.35em;
  position: absolute;
  @include center;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background: radial-gradient(50% 50% at 50% 50%, rgba(255, 255, 255, 0.00) 80.1%, rgba(255, 255, 255, 0.20) 100%), linear-gradient(90deg, #FB9C4E 0%, #EB2149 50%, #A02EB2 100%);
    animation: pulse 3s ease-in-out infinite;
  }
  img{
    width: 8.5em;
    position: relative;
    z-index: 2;
  }
}

.why{
  padding-top: 0;
  position: relative;
  z-index: 2;
  background-color: $light100;
  ._anchor__target{
    @media screen and (min-width: 993px){
      bottom: unset;
      top: 18em;
    }
  }
  .try-free{
    margin-top: -2.875em;
    margin-bottom: 4.2em;
  }
  &__inner{
    position: relative;
  }
  &__title{
    width: 8em;
    margin: 0 auto;
    text-align: center;
  }
  &__items{
    margin-top: 5.5em;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 8.25em / 4));
    gap: 2.75em;
  }
  &__icon{
    left: 1.73em;
    top: 19.36em;
    transform: rotate(-13.747deg);
  }
  &__icon2{
    top: 11.8125em;
    right: 4.5em;
    width: 10.76em;
    height: 10.76em;
    transform: rotate(17.36deg);
    img{
      width: 5.62em;
    }
  }
}

.why-card{
  border-radius: 1.75em;
  background-color: $white;
  padding: 2.75em;
  &:hover{
    .why-card__img{
      border-color: $light100;
      img{
        transform: translateY(-.25em);
      }
    }
    .why-card__title{
      background: linear-gradient(90deg, #FB9C4E 0%, #EB2149 50%, #A02EB2 100%);
      background-clip: text;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }
  }
  &__img{
    border: 3px solid $white;
    border-radius: 1.75em;
    box-shadow: 0px 4px 0px 0px #ECE6F9;
    @include center;
    height: 11.4375em;
    transition: all .3s;
    img{
      width: 6.5625em;
      transition: all .3s;
    }
  }
  &__text{
    text-align: center;
    @include grid100gap(1.375em);
    margin-top: 2.75em;
  }
  &__title{
    text-transform: uppercase;
  }
  &__desc{
    color: $black70;
  }
}



@keyframes pulse {
  0% {
    transform: scale(.9);
  }
  70% {
    transform: scale(1);
  }
  100% {
    transform: scale(.9);
  }
}


@media screen and (max-width: 992px){
  ._anchor{
    &__target{
      margin-bottom: 4em;
    }
  }

  .why{
    .try-free{
      margin-top: 0;
      margin-bottom: 1.875em;
    }
    &__items{
      margin-top: 1.25em;
      grid-template-columns: repeat(2,calc(50% - .3125em));
      gap: .625em;
    }
    &__bottom{
      position: relative;
    }
    &__icon{
      top: 1em;
      left: .2em;
    }
    &__icon2{
      width: 2.5em;
      height: 2.5em;
      top: -.875em;
      right: .25em;
      img{
        width: 1.3125em;
      }
    }
  }

  .try-free{
    margin-left: -.9375em;
    width: calc(100% + 1.875em);
    text-align: center;
    &__inner{
      border-radius: 0;
      padding-left: 0;
      padding-right: 0;
      &:hover{
        width: 100%;
        .try-free__inner2{
          padding: 0;
        }
      }
    }
    &__wrap{
      padding: 1.975em 1em;
      grid-template-columns: 100%;
    }
    &__col{
      display: none;
    }
    &__col2{
      align-items: center;
    }
    &__title{
      font-size: 2.25em;
    }
    &__shops{
      justify-content: center;
    }
    &__shop{
      height: 2.5em;
      padding: .44em .625em;
      border-radius: .4375em;
      border-width: 1px;
      margin-right: .9375em;
    }
  }

  .why-card{
    padding: 1.25em;
    display: flex;
    align-items: center;
    &__img{
      border-radius: 1.4375em;
      width: 4em;
      height: 4em;
      flex-shrink: 0;
      margin-right: 1.25em;
      img{
        width: 2.75em;
      }
    }
    &__text{
      gap: .625em;
      text-align: left;
      margin: 0;
    }
    &__title{
      font-size: 1.125em;
      line-height: 144%;
    }
    &__desc{
      font-size: 1em;
      line-height: 138%;
    }
  }

  .m-icon{
    width: 2.75em;
    height: 2.75em;
    img{
      width: 1.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .why{
    &__items{
      grid-template-columns: 100%;
      gap: .3125em;
    }
  }
}
