.video-custom-play{
  position:relative;
  &:before{
    content:'';
    position:absolute;
    left:0;
    right:0;
    top:0;
    bottom:0;
    background: linear-gradient(180deg, rgba(5, 0, 15, 0.50) 0%, rgba(5, 0, 15, 0.00) 100%);
    z-index:1;
    transition: all .3s;
  }
  &:hover{
    &:before{
      opacity: 0;
    }
  }
  video{
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  .video-play-icon{
    width: 3.75em;
    height: 3.75em;
    @include centerAbsolute;
    z-index:2;
    @include center;
    cursor:pointer;
    img{
      width: 100%;
    }
  }
  &.is-video-playing .video-play-icon{
    opacity:0;
  }
  &.is-video-playing:hover .video-play-icon{
    opacity:1;
  }
  &.is-video-playing .video-play-icon:before{
    content:"";
  }
  &.is-video-playing:before{
    display:none;
  }
}

.how-work{
  overflow: hidden;
  position: relative;
  &__inner{
    position: relative;
  }
  &__icon{
    right: -2.125em;
    top: 7.625em;
    transform: rotate(11.343deg);
    width: 25.8em;
    height: 25.8em;
    img{
      width: 13.4em;
    }
  }
  &__icon2{
    right: 26.5em;
    top: 8.25em;
    width: 7.52em;
    height: 7.52em;
    img{
      width: 3.93em;
    }
  }
  &__icon3{
    left: 38.75em;
    bottom: -1em;
    width: 7.5em;
    height: 7.5em;
    transform: rotate(-11.343deg);
    img{
      width: 4em;
    }
  }
  &__wrap{
    display: grid;
    grid-template-columns: 20.5em calc(100% - 26em);
    gap: 5.5em;
    margin-top: 5.5em;
    padding-left: 11.75em;
  }
  &__items{
    margin-top: 2.75em;
    @include grid100gap(2.75em);
  }
  &__item{
    padding: .1875em;
    border-radius: 3.25em;
    margin-right: auto;
    color: $white;
    position: relative;
    background: linear-gradient(90deg, #FB9C4E 0%, #EB2149 50%, #A02EB2 100%);
    &-inner{
      display: inline-grid;
      grid-template-columns: 7.125em calc(100% - 7.125em);
      align-items: center;
      background: $dark200;
      padding: 2.75em;
      border-radius: inherit;
    }
    &:before{
      content: '';
      position: absolute;
      top: 0;
      right: 100%;
      height: 100%;
      width: 9.75em;
      background-image: url(../img/arrow.svg);
      background-size: 100% auto;
      background-repeat: no-repeat;
      pointer-events: none;
    }
    &:nth-child(1){
      &:before{
        opacity: 0;
      }
    }
    &-num{
      color: $blue100;
    }
    &:nth-child(2){
      margin-left: 9.75em;
    }
    &:nth-child(3){
      margin-left: 19.5em;
    }
  }
}

.phone{
  border-color: $blue100;
  position: relative;
  pointer-events: none;
  &:before,
  &:after{
    content: '';
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    z-index: 3;
  }
  &:before{
    border-radius: 1.9375em;
    width: 4.6875em;
    height: 1.3125em;
    background-color: $black;
    top: .875em;
  }
  &:after{
    width: 6.6875em;
    height: .3125em;
    background-color: $white;
    border-radius: .25em;
    bottom: .75em;
  }
  .video-play-icon{
    opacity: 0;
    pointer-events: none;
  }
}


@media screen and (max-width: 992px){
  .video-custom-play{
    .video-play-icon{
      width: 3.0625em;
      height: 3.0625em;
    }
    &.is-video-playing:hover .video-play-icon{
      opacity:0;
    }
  }

  .how-work{
    padding-bottom: .9375em;
    &__icon{
      display: none;
    }
    &__icon2{
      right: 2.5em;
      top: -1em;
      width: 5.25em;
      height: 5.25em;
      img{
        width: 2.75em;
      }
    }
    &__icon3{
      left: unset;
      bottom: unset;
      top: 5em;
      right: -3.75em;
      width: 10.5em;
      height: 10.5em;
      img{
        width: 5.5em;
      }
    }
    &__wrap{
      grid-template-columns: calc(30% - .625em) calc(70% - .625em);
      gap: 1.25em;
      margin-top: 1.25em;
      padding-left: 0;
    }
    &__items{
      margin-top: 0;
      gap: .3125em;
    }
    &__item{
      border-radius: 1.75em;
      &-inner{
        grid-template-columns: 4.8125em calc(100% - 4.8125em);
        padding: 1.25em;
      }
      &:before{
        width: 4.25em;
        background-image: url(../img/arrow2.svg);
      }
      &:nth-child(2){
        margin-left: 4.25em;
      }
      &:nth-child(3){
        margin-left: 8.5em;
      }
      &-num{
        font-size: 3em;
      }
      &-desc{
        font-size: 1em;
        line-height: 138%;
      }
    }
  }

  .phone{
    border-radius: 2.25em;
    border-width: .225em;
    &:before{
      border-radius: 1.3125em;
      width: 3.25em;
      height: .85em;
      top: .5625em;
    }
    &:after{
      width: 4.6875em;
      height: .225em;
      border-radius: .1875em;
      bottom: .5em;
    }
  }
}


@media screen and (max-width: 576px){
  .how-work{
    &__icon2{
      top: 10.25em;
      right: .625em;
    }
    &__icon3{
      top: 17.875em;
    }
    &__wrap{
      grid-template-columns: 100%;
    }
    &__col{
      width: 14.375em;
    }
    &__items{
      padding-left: 4.5625em
    }
    &__item{
      margin-left: 0 !important;
      &:before{
        margin-right: .3125em;
      }
      br{
        display: none;
      }
      &:nth-child(1){
        &:before{
          opacity: 1;
        }
      }
      &:nth-child(3){
        &:before{
          background-image: url(../img/arrow3.svg);
        }
      }
    }
  }
}
