.header,
.main{
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url(../img/bg.png);
    background-size: cover;
    background-position: center;
    mix-blend-mode: color-burn;
    animation: pulse2 4s ease-in-out infinite;
    transform: scale(1.02);
  }
}

.main{
  overflow: hidden;
  color: $white;
  background: linear-gradient(90deg, #FB9C4E 0%, #EB2149 50%, #A02EB2 100%);
  position: relative;
  &__inner{
    padding: 12.375em 11.5em 15em;
    position: relative;
  }
  &__content{
    max-width: 45em;
  }
  &__subtitle{
    margin-top: 3.5em;
  }
  &__items{
    display: flex;
    margin-top: -.25em;
  }
}

@keyframes pulse2 {
  0% {
    transform: scale(1.02);
  }
  70% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.02);
  }
}

.main-card{
  width: 8.5em;
  flex-shrink: 0;
  text-align: center;
  white-space: nowrap;
  &._active{
    .main-card__rect{
      opacity: 1;
      transform: rotate(90deg);
    }
  }
  &:nth-child(1){
    margin-top: 2.75em;
  }
  &:nth-child(2){
    margin-top: 2.125em;
  }
  &:nth-child(3){
    margin-top: 1.25em;
  }
  &:nth-child(4){
    margin-top: .625em;
  }
  &__top{
    position: relative;
  }
  &__icon{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    @include center;
    img{
      width: 2.25em;
    }
  }
  &__rect{
    transition: all .4s;
    width: 100%;
    display: block;
    opacity: .8;
    transition: .3s ease-in-out;
  }
}

.main-media{
  position: absolute;
  bottom: 0;
  right: 5.5em;
  width: 50.375em;
  &__cube{
    img{
      width: 100%;
      display: block;
    }
  }
  &__phone,
  &__phone2{
    position: absolute;
    &:before{
      content: '';
      position: absolute;
      width: 14.6875em;
      height: 1.75em;
      @include bgImgSize(100%);
    }
    img{
      width: 100%;
      display: block;
      position: relative;
      z-index: 2;
    }
  }
  &__phone{
    width: 22.75em;
    right: 4em;
    bottom: 100%;
    margin-bottom: -4.5em;
    img{
      animation: rotate 3s ease-in-out infinite;
    }
    &:before{
      background-image: url(../img/shadow.svg);
      right: 2em;
      bottom: 1.75em;
      width: 15.1875em;
    }
  }
  &__phone2{
    width: 23.22em;
    left: 9em;
    bottom: 100%;
    margin-bottom: -4.5em;
    img{
      animation: rotate2 3s ease-in-out infinite;
    }
    &:before{
      background-image: url(../img/shadow2.svg);
      left: 1em;
      bottom: .5em;
      width: 17.3125em;
    }
  }
}

@keyframes rotate {
  from {
    transform: rotate(0deg) translateY(0);
  }
  50% {
    transform: rotate(-2deg) translateY(-.5em);
  }
  to {
    transform: rotate(0deg) translateY(0);
  }
}

@keyframes rotate2 {
  from {
    transform: rotate(0deg) translateY(0);
  }
  50% {
    transform: rotate(2deg) translateY(-.5em);
  }
  to {
    transform: rotate(0deg) translateY(0);
  }
}


@media screen and (max-width: 992px){
  .main{
    &__inner{
      padding: 5.5em 1.25em;
    }
    &__content{
      max-width: 23em;
    }
    &__subtitle{
      margin-top: .625em;
    }
    &__items{
      width: calc(100% + .9375em);
      flex-wrap: wrap;
      margin: 0;
    }
  }

  .main-card{
    width: 6.125em;
    margin: .625em .625em 0 0 !important;
    flex-shrink: 0;
    &__top{
      padding: 0 .3125em;
    }
    &__icon{
      @include center;
      img{
        width: 1.75em;
      }
    }
    &__text{
      font-size: 1em;
      line-height: 125%;
    }
  }

  .main-media{
    font-size: .5em;
  }
}


@media screen and (max-width: 576px){
  .main,
  .header{
    &:before{
      background-image: url(../img/main-bg4.png);
      transform: scale(1.1);
    }
  }

  .main{
    text-align: center;
    &__inner{
      padding-bottom: 0;
    }
    &__content{
      max-width: 100%;
      margin-bottom: 17.5em;
    }
    &__items{
      justify-content: center;
    }
  }

  @keyframes pulse2 {
    0% {
      transform: scale(1.1);
    }
    70% {
      transform: scale(1);
    }
    100% {
      transform: scale(1.1);
    }
  }
  
  .main-media{
    font-size: .63em;
    position: relative;
    width: calc(100% + 2em);
    margin-left: -1em;
    right: unset;
    &__phone,
    &__phone2{
      font-size: .75em;
    }
    &__phone{
      right: 2em;
    }
    &__phone2{
      left: 6em;
    }
  }
}
