.footer{
  padding: 2.75em 0;
  background-color: $dark100;
  color: $white;
  &__wrap{
    margin: 2.75em auto 0;
    max-width: 100%;
    width: 57.125em;
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 11em / 3));
    gap: 5.5em;
  }
  &__bottom{
    margin-top: 2.75em;
    padding-top: 2.75em;
    border-top: .1875em solid $dark200;
    text-align: center;
  }
}

.contact-item{
  @include grid100gap(.5em);
  text-align: center;
}


@media screen and (max-width: 992px){
  .footer{
    padding: 1.875em 0;
    &__wrap{
      margin-top: 1.25em;
      width: 40em;
      grid-template-columns: repeat(3, calc(100% / 3 - 2.75em / 3));
      gap: 1.375em;
    }
    &__bottom{
      margin-top: 1.25em;
      padding-top: 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    &__wrap{
      grid-template-columns: 100%;
    }
  }
}
