.faq{
  background-color: $light100;
  overflow: hidden;
  position: relative;
  &__inner{
    position: relative;
  }
  &__icon{
    top: -1.875em;
    right: 10.625em;
    width: 10.625em;
    height: 10.625em;
    transform: rotate(13.435deg);
    img{
      width: 5.5em;
    }
  }
  &__items{
    margin-top: 5.5em;
    @include grid100gap(1.375em);
  }
  .try-free{
    margin-top: 5.5em;
  }
}

.qa-card{
  border-radius: 1.75em;
  background: $white;
  transition: all .3s;
  @media screen and (min-width: 993px){
    &:hover{
      background: $blue100;
    }
  }
  &._active{
    .qa-card__btn img{
      transform: rotate(-180deg);
    }
  }
  &__show{
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    padding: 2.75em;
  }
  &__btn{
    margin-left: 1em;
    width: 3em;
    @include center;
    flex-shrink: 0;
    img{
      transition: all .3s;
      width: 100%;
    }
  }
  &__hidden{
    padding: 0 2.75em 2.75em;
    display: none;
  }
}

.feedback{
  position: relative;
  &__inner{
    background-color: $light100;
    background-image: url(../img/feedback-bg.svg);
    background-size: cover;
    background-position: center;
    padding: 5.5em 9.375em;
    border-radius: 5.5em;
    position: relative;
  }
  &__icon{
    position: absolute;
    top: -2.125em;
    left: 6.375em;
    width: 14.125em;
    height: 14.125em;
    transform: rotate(-11.108deg);
    img{
      width: 7.375em;
    }
  }
}

.slider{
  &__wrap{
    overflow: hidden;
  }
}

.feedback-slider{
  margin-top: 5.5em;
  position: relative;
  .swiper{
    &-container{
      width: calc(100% + 2.75em);
    }
    &-slide{
      width: 25%;
      padding-right: 2.75em;
    }
    &-button{
      transition: all .3s;
      top: 50%;
      margin: 0;
      transform: translateY(-50%);
      width: 8em;
      height: 20.75em;
      border-radius: 3.25em;
      background-color: $light100;
      @include center;
      &:hover{
        background-color: $white;
      }
      img{
        width: 5em;
      }
      &-prev{
        left: -10.75em;
      }
      &-next{
        right: -10.75em;
      }
    }
  }
}

.phone,
.feedback-card{
  border-radius: 3.25em;
  border: .3125em solid $white;
  background-color: $dark300;
  &__inner{
    padding-bottom: 201%;
    position: relative;
    border-radius: inherit;
    overflow: hidden;
  }
  .video-custom-play{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: $white;
    border-radius: inherit;
    z-index: 2;
  }
}

.feedback-card{
  position: relative;
  border: none;
  padding: .3125em;
  background-color: $white;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: inherit;
    background: linear-gradient(90deg, #FB9C4E 0%, #EB2149 50%, #A02EB2 100%);
    transition: all .3s;
    opacity: 0;
  }
  &:hover{
    @media screen and (min-width: 993px){
      &:before{
        opacity: 1;
      }
    }
  }
  &__inner{
    z-index: 2;
  }
}


@media screen and (max-width: 992px){
  .faq{
    padding-bottom: 0;
    &__icon{
      top: -1em;
      right: -1.5em;
      width: 4.3125em;
      height: 4.3125em;
      img{
        width: 2.25em;
      }
    }
    .h1{
      max-width: 80%;
      margin: 0 auto;
    }
    &__items{
      margin-top: 1.25em;
      gap: .3125em;
    }
    .try-free{
      margin-top: 1.875em;
    }
  }

  .qa-card{
    &__show{
      padding: 1.25em;
    }
    &__btn{
      width: 1.5em;
    }
    &__hidden{
      padding: 0 1.25em 1.25em;
    }
  }

  .feedback{
    padding: .9375em 0;
    &__inner{
      padding: 1.875em 0;
      border-radius: 4.125em;
    }
    &__icon{
      top: .4375em;
      left: -.375em;
      width: 3.75em;
      height: 3.75em;
      img{
        width: 2em;
      }
    }
  }

  .feedback-slider{
    padding-left: 2.94em;
    margin-top: 1.25em;
    .slider__wrap{
      padding: 0 2.94em 0 0;
    }
    .swiper {
      &-container {
        width: calc(100% + .9375em);
      }

      &-slide {
        width: 17.67em;
        padding-right: .9375em;
      }

      &-button {
        display: none;
      }
    }
  }

  .feedback-card{
    border-radius: 2.65em;
    padding: .25em;
  }
}


@media screen and (max-width: 576px){
  .feedback{
    .h1{
      width: 58%;
      margin: 0 auto;
    }
  }

  .feedback-slider{
    overflow: hidden;
    border-radius: 0 0 2em 2em;
    padding-left: 0;
    .slider__wrap{
      padding: 0;
      width: 17.67em;
      overflow: unset;
      margin: 0 auto;
    }
    .swiper {
      &-slide {
        width: 100%;
        &-active{
          .feedback-card{
            background: linear-gradient(90deg, #FB9C4E 0%, #EB2149 50%, #A02EB2 100%);
          }
        }
      }
    }
  }

  .feedback-card{
    background: transparent !important;
    padding: 0;
    border: .1875em solid $white;
    &:before{
      display: none;
    }
  }
}
